.stepper-full{
    width: 96px;
    height: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.stepper-full__small-box{
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0px 7px 64px rgba(0, 0, 0, 0.07);
    border-radius: 8px;
    color:#ECEBED;
    z-index:10;
}
.stepper-full__large-box{
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0px 7px 64px rgba(0, 0, 0, 0.07);
    border-radius: 8px;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
    line-height: 20px;
    font-size: 15px;
    color: #6979F8;
}